.grid-display {
    display: grid;
}

.generic-button {
    background: transparent; 
    border: 2px solid transparent; 
    border-radius: 50%; 
    font-size: 20px; 
    color: black; 
    cursor: pointer;
    width: 40px; 
    height: 40px; 
    padding: 0; 
    display: flex; 
    justify-content: center; 
    align-items: center; 
}

/* Hover effect */
.generic-button:hover {
    color: #878787; 
    /* background-color: rgb(47, 47, 47);  */
}